const MIN_DIST_SEC = 0.1,
  SEARCH_WIN_SEC = 0.15,
  T_WAVE_SLOPE_RATIO = 0.6;
const RESCUE_SCALE = 0.5,
  GAPFILL_LONG_SEC = 0.9,
  GAPFILL_RR_FACTOR = 0.95,
  GAPFILL_LOW_SCALE = 0.18,
  DEDUP_SEC = 0.22;
const ABS_REFRACT_SEC = 0.2,
  REL_REFRACT_FRAC = 0.4;
const RR_GUIDE_ENABLE = true,
  RR_GUIDE_MIN_FRAC = 0.55,
  RR_GUIDE_STRONG_MULT = 1.6;
const RS_DROP_WIN_SEC = 0.05,
  RS_DROP_FRAC = 0.18;
const MICRO_R_ENABLE = false,
  MICRO_R_LO_SEC = 0.28,
  MICRO_R_HI_SEC = 0.52,
  MICRO_R_PROM_FRAC = 0.33,
  MICRO_R_MIN_POS_FRAC = 0.2,
  MICRO_R_SIDE_SEC = 0.06;
const SHAPE_FILTER = true,
  SHAPE_WIDTH_MAX_SEC = 0.12,
  SHAPE_WIDTH_MIN_SEC = 0.012,
  SHAPE_SLOPE_MIN_FRAC = 0.06,
  SHAPE_PROM_FRAC = 0.12,
  SHAPE_CURV_MIN_FRAC = 0.03;

const R = Math.round;

function mean(a, start = 0, end = a.length) {
  const n = Math.max(0, end - start);
  if (!n) return 0;
  let s = 0;
  for (let i = start; i < end; i++) s += a[i];
  return s / n;
}
function median(arr) {
  const n = arr.length;
  if (!n) return 0;
  const s = new Float64Array(n);
  for (let i = 0; i < n; i++) s[i] = arr[i];
  s.sort();
  const m = n >> 1;
  return n & 1 ? s[m] : 0.5 * (s[m - 1] + s[m]);
}
function maxAbs(a) {
  let m = 0;
  for (let i = 0; i < a.length; i++) {
    const v = Math.abs(a[i]);
    if (v > m) m = v;
  }
  return m;
}
function minMax(a) {
  let mn = Infinity,
    mx = -Infinity;
  for (let i = 0; i < a.length; i++) {
    const v = a[i];
    if (v < mn) mn = v;
    if (v > mx) mx = v;
  }
  return [mn, mx];
}
function normalizeMaxAbsInPlace(x) {
  const m = maxAbs(x);
  if (!m) return;
  const s = 1 / m;
  for (let i = 0; i < x.length; i++) x[i] *= s;
}
function argmaxRange(x, s, e) {
  s = s | 0;
  e = Math.min(e | 0, x.length);
  if (e <= s) return { idx: -1, val: -Infinity };
  let im = s,
    vm = x[s];
  for (let i = s + 1; i < e; i++) {
    const v = x[i];
    if (v > vm) {
      vm = v;
      im = i;
    }
  }
  return { idx: im, val: vm };
}
function dedupPeaks(idxs, minDist) {
  if (idxs.length === 0) return [];
  const out = [idxs[0]];
  for (let i = 1; i < idxs.length; i++)
    if (idxs[i] - out[out.length - 1] >= minDist) out.push(idxs[i]);
  return out;
}
function isUphillPeak(sig, idx, pre = 4, post = 4) {
  const i0 = Math.max(0, idx - pre);
  const i1 = Math.min(sig.length - 1, idx + post);
  if (i1 - i0 < 3) return true;
  return sig[idx] - sig[i0] > 0 && sig[i1] - sig[idx] < 0;
}
function localProminence(sig, idx, side) {
  const L = Math.max(0, idx - side);
  const R1 = Math.min(sig.length - 1, idx + side);
  let ml = Infinity,
    mr = Infinity;
  for (let i = L; i <= idx; i++) if (sig[i] < ml) ml = sig[i];
  for (let i = idx; i <= R1; i++) if (sig[i] < mr) mr = sig[i];
  return sig[idx] - Math.max(ml, mr);
}
function localRange(sig, idx, side) {
  const L = Math.max(0, idx - side);
  const R1 = Math.min(sig.length - 1, idx + side);
  let mn = Infinity,
    mx = -Infinity;
  for (let i = L; i <= R1; i++) {
    const v = sig[i];
    if (v < mn) mn = v;
    if (v > mx) mx = v;
  }
  const r = mx - mn;
  return r > 1e-9 ? r : 1e-9;
}
function widthAtHalfProm(sig, idx, side) {
  const thr = sig[idx] - 0.5 * localProminence(sig, idx, side);
  const L0 = Math.max(0, idx - side);
  const R0 = Math.min(sig.length - 1, idx + side);
  let L = idx,
    Ri = idx;
  while (L > L0 && sig[L] >= thr) L--;
  while (Ri < R0 && sig[Ri] >= thr) Ri++;
  return Ri - L;
}
function maxSlopesAround(sig, idx, side) {
  const L0 = Math.max(0, idx - side);
  const R0 = Math.min(sig.length - 2, idx + side);
  let up = 0,
    down = 0;
  for (let i = L0; i < idx; i++) {
    const d = sig[i + 1] - sig[i];
    if (d > up) up = d;
  }
  for (let i = idx; i <= R0; i++) {
    const d = sig[i + 1] - sig[i];
    if (d < down) down = d;
  }
  return { up, down };
}
function passesQRSShape(sig, idx, fs, SIG_LEV1, NOISE_LEV1) {
  const side = R(0.12 * fs);
  const sideSlope = R(0.02 * fs);
  const prom = localProminence(sig, idx, side);
  const rng = localRange(sig, idx, side);
  const w = widthAtHalfProm(sig, idx, side);
  const iL = Math.max(1, idx - 1),
    iR = Math.min(sig.length - 2, idx + 1);
  const curv = -(sig[iL] - 2 * sig[idx] + sig[iR]) / rng;
  const { up, down } = maxSlopesAround(sig, idx, sideSlope);
  const promThr = Math.max(
    SHAPE_PROM_FRAC * Math.max(1e-9, SIG_LEV1 - NOISE_LEV1),
    0.06 * Math.max(1e-9, SIG_LEV1),
  );
  const wMin = R(SHAPE_WIDTH_MIN_SEC * fs);
  const wMax = R(SHAPE_WIDTH_MAX_SEC * fs);
  const slopeThr = SHAPE_SLOPE_MIN_FRAC * rng;
  return !(
    prom < promThr ||
    w < wMin ||
    w > wMax ||
    curv < SHAPE_CURV_MIN_FRAC ||
    up < slopeThr ||
    -down < slopeThr
  );
}
function postPeakDropOK(sig, idx, fs) {
  const win = R(RS_DROP_WIN_SEC * fs);
  const Rb = Math.min(sig.length - 1, idx + win);
  let mn = Infinity;
  for (let i = idx; i <= Rb; i++) {
    const v = sig[i];
    if (v < mn) mn = v;
  }
  return sig[idx] - mn >= RS_DROP_FRAC * localRange(sig, idx, R(0.12 * fs));
}

function flattop(M) {
  const a0 = 1,
    a1 = 1.93,
    a2 = 1.29,
    a3 = 0.388,
    a4 = 0.028;
  const N = M - 1;
  const w = new Float32Array(M);
  for (let n = 0; n < M; n++) {
    const p = (2 * Math.PI * n) / N;
    w[n] =
      a0 - a1 * Math.cos(p) + a2 * Math.cos(2 * p) - a3 * Math.cos(3 * p) + a4 * Math.cos(4 * p);
  }

  let s = 0;
  for (let i = 0; i < M; i++) s += w[i];
  const inv = 1 / s;
  for (let i = 0; i < M; i++) w[i] *= inv;
  return w;
}
function designFIRLowpass(numTaps, fc, fs) {
  const M = numTaps - 1;
  const w = new Float32Array(numTaps);
  for (let n = 0; n < numTaps; n++) w[n] = 0.54 - 0.46 * Math.cos((2 * Math.PI * n) / M);
  const h = new Float32Array(numTaps);
  const norm = (2 * fc) / fs;
  for (let n = 0; n < numTaps; n++) {
    const m = n - M / 2;
    const x = norm * m;
    const sinc = x === 0 ? 1 : Math.sin(Math.PI * x) / (Math.PI * x);
    h[n] = norm * sinc * w[n];
  }
  let s = 0;
  for (let i = 0; i < numTaps; i++) s += h[i];
  const inv = 1 / s;
  for (let i = 0; i < numTaps; i++) h[i] *= inv;
  return h;
}

const BUTTER_200_LOW_12HZ = {
  b: new Float32Array([0.00475052, 0.01425157, 0.01425157, 0.00475052]),
  a: new Float32Array([1.0, -2.25008508, 1.75640138, -0.46831211]),
};
const BUTTER_200_HIGH_5HZ = {
  b: new Float32Array([0.85449723, -2.56349169, 2.56349169, -0.85449723]),
  a: new Float32Array([1.0, -2.6861574, 2.41965511, -0.73016535]),
};

function lfilter3(b, a, x, y) {
  const b0 = b[0],
    b1 = b[1],
    b2 = b[2],
    b3 = b[3];
  const a1 = a[1],
    a2 = a[2],
    a3 = a[3];
  let x1 = 0,
    x2 = 0,
    x3 = 0,
    y1 = 0,
    y2 = 0,
    y3 = 0;
  for (let n = 0; n < x.length; n++) {
    const xn = x[n];
    const yn = b0 * xn + b1 * x1 + b2 * x2 + b3 * x3 - (a1 * y1 + a2 * y2 + a3 * y3);
    y[n] = yn;
    x3 = x2;
    x2 = x1;
    x1 = xn;
    y3 = y2;
    y2 = y1;
    y1 = yn;
  }
}
function lfilterFIR(b, x, y) {
  const M = b.length;
  for (let n = 0; n < x.length; n++) {
    let acc = 0;
    for (let k = 0; k < M; k++) {
      const i = n - k;
      if (i >= 0) acc += b[k] * x[i];
      else break;
    }
    y[n] = acc;
  }
}
function lfilterGeneric(b, a, x, y) {
  const nb = b.length,
    na = a.length;
  const a0 = a[0];
  for (let n = 0; n < x.length; n++) {
    let acc = 0;
    for (let i = 0; i < nb; i++) if (n - i >= 0) acc += b[i] * x[n - i];
    for (let j = 1; j < na; j++) if (n - j >= 0) acc -= a[j] * y[n - j];
    y[n] = acc / a0;
  }
}
class FilterRunner {
  constructor() {
    this.padBuf1 = new Float32Array(0);
    this.padBuf2 = new Float32Array(0);
  }
  _ensurePad(len) {
    if (this.padBuf1.length < len) {
      this.padBuf1 = new Float32Array(len);
      this.padBuf2 = new Float32Array(len);
    }
  }
  filtfilt(b, a, x, padlen = null) {
    const edge = Math.max(b.length, a.length) - 1;
    const pl = padlen == null ? 3 * edge : padlen;
    const n = x.length;
    const L = pl + n + pl;
    this._ensurePad(L);
    const t1 = this.padBuf1,
      t2 = this.padBuf2;
    for (let i = 0; i < pl; i++) t1[i] = 2 * x[0] - x[Math.min(pl - i, n - 1)];
    t1.set(x, pl);
    for (let i = 0; i < pl; i++) t1[pl + n + i] = 2 * x[n - 1] - x[Math.max(n - 2 - i, 0)];
    if (a.length === 1) lfilterFIR(b, t1.subarray(0, L), t2.subarray(0, L));
    else if (a.length === 4 && b.length === 4) lfilter3(b, a, t1.subarray(0, L), t2.subarray(0, L));
    else lfilterGeneric(b, a, t1.subarray(0, L), t2.subarray(0, L));
    for (let i = 0; i < L; i++) t1[i] = t2[L - 1 - i];

    if (a.length === 1) lfilterFIR(b, t1.subarray(0, L), t2.subarray(0, L));
    else if (a.length === 4 && b.length === 4) lfilter3(b, a, t1.subarray(0, L), t2.subarray(0, L));
    else lfilterGeneric(b, a, t1.subarray(0, L), t2.subarray(0, L));
    for (let i = 0; i < n; i++) x[i] = t2[L - 1 - (pl + i)];
    return x;
  }
}
function smootherFlatTopSame(signal, size, out, winCache) {
  const n = signal.length;
  const m = Math.max(1, Math.min(size | 0, n - 1));
  const wKey = m;
  let w = winCache.get(wKey);
  if (!w) {
    w = flattop(m);
    winCache.set(wKey, w);
  }
  const half = (m - 1) >> 1;

  for (let i = 0; i < n; i++) {
    let acc = 0;
    for (let k = 0; k < m; k++) {
      let j = i + k - half;
      if (j < 0) j = -j;
      if (j >= n) j = 2 * n - 2 - j;
      acc += signal[j] * w[k];
    }
    out[i] = acc;
  }
}

function movingAvgSame(src, win, out) {
  const n = src.length;
  const m = Math.max(1, win | 0);
  const left = (m - 1) >> 1;
  const right = m - 1 - left;

  for (let i = 0; i < n; i++) {
    let L = i - left,
      R = i + right;
    if (L < 0) L = 0;
    if (R >= n) R = n - 1;
    let acc = 0;
    for (let j = L; j <= R; j++) acc += src[j];
    out[i] = acc / (R - L + 1);
  }
}

function peakIndexes(y, thres = 0, min_dist = 1) {
  const n = y.length;
  if (n < 3) return [];
  const [ymin, ymax] = minMax(y);
  const absThres = ymin + thres * (ymax - ymin);
  const cand = [];
  for (let i = 1; i < n - 1; i++) {
    const yi = y[i];
    if (yi > absThres && yi > y[i - 1] && yi > y[i + 1]) cand.push(i);
  }
  if (!cand.length) return [];
  cand.sort((i, j) => y[j] - y[i]);
  const taken = [];
  const bad = new Uint8Array(n);
  for (let t = 0; t < cand.length; t++) {
    const idx = cand[t];
    if (bad[idx]) continue;
    taken.push(idx);
    const s = Math.max(0, idx - min_dist);
    const e = Math.min(n - 1, idx + min_dist);
    for (let k = s; k <= e; k++) bad[k] = 1;
  }
  taken.sort((a, b) => a - b);
  return taken;
}

class Pan_Tompkins_Plus_Plus_Plus {
  constructor() {
    this.bufA = new Float32Array(0);
    this.bufB = new Float32Array(0);
    this.bufC = new Float32Array(0);
    this.bufD = new Float32Array(0);
    this.bufE = new Float32Array(0);

    this.fr = new FilterRunner();

    this.winCache = new Map();
    this.boxWinCache = new Map();
    this.diffByFs = new Map();

    this.h129_400 = designFIRLowpass(129, 80, 400);

    this.minRRsamples = null;
    this.minRRsec = null;
    this.minRRms = null;
  }

  get_name() {
    return 'Pan_Tompkins_Plus_Plus_Plus';
  }

  _ensure(n) {
    if (this.bufA.length < n) {
      this.bufA = new Float32Array(n);
      this.bufB = new Float32Array(n);
      this.bufC = new Float32Array(n);
      this.bufD = new Float32Array(n);
      this.bufE = new Float32Array(n);
    }
  }

  _getDiff(fs) {
    let diff = this.diffByFs.get(fs);
    if (!diff) {
      diff = new Float32Array([1, 2, 0, -2, -1].map((v) => (v * fs) / 8));
      this.diffByFs.set(fs, diff);
    }
    return diff;
  }

  _getBoxWinLen(fs, frac) {
    const L = Math.max(1, R(frac * fs));
    return L;
  }

  downsample400to200(x400) {
    const n = x400.length;
    this._ensure(n);
    this.bufA.set(x400, 0);
    const pad = 3 * (this.h129_400.length - 1);
    this.fr.filtfilt(this.h129_400, new Float32Array([1]), this.bufA.subarray(0, n), pad);

    const n2 = n >> 1;
    const out = new Float32Array(n2);
    for (let k = 0; k < n2; k++) out[k] = this.bufA[2 * k];
    return out;
  }

  rpeak_detection_200_pos(ecg, fs) {
    const n = ecg.length;
    this._ensure(n);
    const mu = mean(ecg);
    for (let i = 0; i < n; i++) this.bufA[i] = ecg[i] - mu;
    this.bufB.set(this.bufA.subarray(0, n), 0);
    this.fr.filtfilt(BUTTER_200_LOW_12HZ.b, BUTTER_200_LOW_12HZ.a, this.bufB.subarray(0, n));
    normalizeMaxAbsInPlace(this.bufB.subarray(0, n));
    this.bufC.set(this.bufB.subarray(0, n), 0);
    const padHP = 3 * (Math.max(BUTTER_200_HIGH_5HZ.b.length, BUTTER_200_HIGH_5HZ.a.length) - 1);
    this.fr.filtfilt(BUTTER_200_HIGH_5HZ.b, BUTTER_200_HIGH_5HZ.a, this.bufC.subarray(0, n), padHP);
    normalizeMaxAbsInPlace(this.bufC.subarray(0, n));
    const b_diff = this._getDiff(fs);
    const padD = 3 * (Math.max(b_diff.length, 1) - 1);
    this.bufD.set(this.bufC.subarray(0, n), 0);
    this.fr.filtfilt(b_diff, new Float32Array([1]), this.bufD.subarray(0, n), padD);
    normalizeMaxAbsInPlace(this.bufD.subarray(0, n));
    for (let i = 0; i < n; i++) this.bufE[i] = this.bufD[i] * this.bufD[i];
    smootherFlatTopSame(
      this.bufE.subarray(0, n),
      R(0.06 * fs),
      this.bufA.subarray(0, n),
      this.winCache,
    );
    const boxLen = this._getBoxWinLen(fs, 0.15);
    movingAvgSame(this.bufA.subarray(0, n), boxLen, this.bufB.subarray(0, n));
    const locs = peakIndexes(this.bufB.subarray(0, n), 0, R(MIN_DIST_SEC * fs));
    const pks = new Float32Array(locs.length);
    for (let i = 0; i < locs.length; i++) pks[i] = this.bufB[locs[i]];

    const qrs_i = [];
    let qrs_i_raw = [];
    const twoSec = 2 * fs + 1;
    let max_m = -Infinity;
    for (let i = 0; i < Math.min(twoSec, n); i++) if (this.bufB[i] > max_m) max_m = this.bufB[i];
    let THR_SIG = max_m / 3;
    let SIG_LEV = THR_SIG;
    let NOISE_LEV = mean(this.bufB, 0, Math.min(twoSec, n)) / 2;

    let max_h = -Infinity;
    for (let i = 0; i < Math.min(twoSec, n); i++) if (this.bufC[i] > max_h) max_h = this.bufC[i];
    let THR_SIG1 = max_h / 3;
    let SIG_LEV1 = THR_SIG1;
    let NOISE_LEV1 = mean(this.bufC, 0, Math.min(twoSec, n)) / 2;

    let Beat_C = 0,
      Check_Flag = 0,
      lastAccepted = -1e9;

    const avgDiffSegment = (arr, s, e) => {
      if (e <= s) return 0;
      let sum = 0;
      for (let i = s + 1; i <= e; i++) sum += arr[i] - arr[i - 1];
      return sum / (e - s);
    };

    const isTWaveLike = (locIdx) => {
      if (Beat_C < 1) return false;
      const seg = R(0.07 * fs);
      const s1 = Math.max(0, locIdx - seg),
        e1 = locIdx;
      const prev = qrs_i[Beat_C - 1];
      const s2 = Math.max(0, prev - seg - 1),
        e2 = prev;
      const slope1 = avgDiffSegment(this.bufB, s1, e1);
      const slope2 = avgDiffSegment(this.bufB, s2, e2);
      return Math.abs(slope1) <= Math.abs(T_WAVE_SLOPE_RATIO * slope2);
    };

    for (let i = 0; i < pks.length; i++) {
      const half = R(SEARCH_WIN_SEC * fs);
      const s = Math.max(0, locs[i] - half);
      const e = Math.min(n, locs[i] + half + 1);
      const { idx: x_i_abs, val: y_i } = argmaxRange(this.bufC, s, e);
      const sinceLastAccepted = locs[i] - lastAccepted;
      if (sinceLastAccepted < R(ABS_REFRACT_SEC * fs)) continue;

      let mean_RR = 0,
        m_selected_RR = 0;
      if (Beat_C >= 9) {
        const diffs = [];
        for (let k = Beat_C - 9; k < Beat_C - 1; k++) diffs.push(qrs_i[k + 1] - qrs_i[k]);
        mean_RR = mean(diffs);
        m_selected_RR = mean_RR;
      }
      const test_m = m_selected_RR || mean_RR || 0;

      let rrMedRecent = null;
      if (Beat_C >= 3) {
        const diffs = [];
        const st = Math.max(1, Beat_C - 8);
        for (let k = st; k < Beat_C; k++) diffs.push(qrs_i[k] - qrs_i[k - 1]);
        rrMedRecent = median(diffs.filter((d) => d > 0));
      }
      const sinceLast = Beat_C > 0 ? locs[i] - qrs_i[Beat_C - 1] : 1e9;
      let rrGateOK = true;
      if (RR_GUIDE_ENABLE && rrMedRecent && isFinite(rrMedRecent)) {
        const early = R(RR_GUIDE_MIN_FRAC * rrMedRecent);
        if (sinceLast < early)
          rrGateOK =
            pks[i] >= RR_GUIDE_STRONG_MULT * THR_SIG && y_i >= RR_GUIDE_STRONG_MULT * THR_SIG1;
      }

      if (Beat_C >= 2) {
        const prevRR = qrs_i[Beat_C - 1] - qrs_i[Beat_C - 2];
        if (sinceLastAccepted < R(REL_REFRACT_FRAC * prevRR)) {
          const veryStrong = pks[i] >= 1.4 * THR_SIG && y_i >= 1.4 * THR_SIG1;
          if (!veryStrong) continue;
        }
      }

      let accepted = false;
      const acceptQRS = (locIdx, amp_m, absIdx_h, amp_h) => {
        if (amp_h <= 0) return;
        if (!isUphillPeak(this.bufC, absIdx_h, 4, 4)) return;
        if (SHAPE_FILTER && !passesQRSShape(this.bufC, absIdx_h, fs, SIG_LEV1, NOISE_LEV1)) return;
        if (!postPeakDropOK(this.bufC, absIdx_h, fs)) return;
        Beat_C++;
        qrs_i.push(locIdx);
        lastAccepted = locIdx;
        if (amp_h >= THR_SIG1) {
          qrs_i_raw.push(absIdx_h);
          SIG_LEV1 = 0.125 * amp_h + 0.875 * SIG_LEV1;
        }
        SIG_LEV = 0.125 * amp_m + 0.875 * SIG_LEV;
        accepted = true;
      };

      if (pks[i] >= THR_SIG && rrGateOK) {
        let veto = false;
        if (
          Beat_C >= 1 &&
          (locs[i] - qrs_i[Beat_C - 1] <= R(0.36 * fs) ||
            (test_m && locs[i] - qrs_i[Beat_C - 1] <= R(0.5 * test_m)))
        ) {
          veto = isTWaveLike(locs[i]);
        }
        if (!veto) acceptQRS(locs[i], pks[i], x_i_abs, y_i);
      }

      if (MICRO_R_ENABLE && !accepted && rrGateOK) {
        const inWin = sinceLast >= R(MICRO_R_LO_SEC * fs) && sinceLast <= R(MICRO_R_HI_SEC * fs);
        if (inWin && y_i > 0 && !isTWaveLike(locs[i])) {
          const side = R(MICRO_R_SIDE_SEC * fs);
          const prom = localProminence(this.bufC, x_i_abs, side);
          const promThr = Math.max(
            MICRO_R_PROM_FRAC * Math.max(1e-9, SIG_LEV1 - NOISE_LEV1),
            MICRO_R_MIN_POS_FRAC * Math.max(1e-9, SIG_LEV1),
          );
          if (
            prom > promThr &&
            isUphillPeak(this.bufC, x_i_abs, 4, 4) &&
            postPeakDropOK(this.bufC, x_i_abs, fs)
          ) {
            acceptQRS(locs[i], pks[i], x_i_abs, y_i);
          }
        }
      }

      if (!accepted && rrGateOK && !isTWaveLike(locs[i])) {
        const okShortRR = sinceLast >= R(0.2 * fs) && sinceLast <= R(0.6 * fs);
        if (pks[i] >= RESCUE_SCALE * THR_SIG && okShortRR && y_i >= RESCUE_SCALE * THR_SIG1)
          acceptQRS(locs[i], pks[i], x_i_abs, y_i);
      }

      if (!accepted) {
        NOISE_LEV1 = 0.125 * Math.max(0, y_i) + 0.875 * NOISE_LEV1;
        NOISE_LEV = 0.125 * pks[i] + 0.875 * NOISE_LEV;
      }
      if (NOISE_LEV || SIG_LEV) THR_SIG = NOISE_LEV + 0.25 * Math.abs(SIG_LEV - NOISE_LEV);
      if (NOISE_LEV1 || SIG_LEV1) THR_SIG1 = NOISE_LEV1 + 0.25 * Math.abs(SIG_LEV1 - NOISE_LEV1);
      Check_Flag = 0;
    }

    (function gapFill(self) {
      let peaks = qrs_i_raw.slice();
      if (peaks.length < 2) {
        qrs_i_raw = peaks;
        return;
      }
      const rrs = [];
      for (let k = 1; k < peaks.length; k++) rrs.push(peaks[k] - peaks[k - 1]);
      let RRexp = median(rrs) || R(0.8 * fs);
      const added = [];
      for (let k = 1; k < peaks.length; k++) {
        const prev = peaks[k - 1],
          next = peaks[k],
          gap = next - prev;
        if (gap > Math.max(GAPFILL_LONG_SEC * fs, GAPFILL_RR_FACTOR * RRexp)) {
          const candM = argmaxRange(self.bufB, prev + R(0.25 * gap), prev + R(0.75 * gap));
          if (candM.idx >= 0) {
            const half = R(SEARCH_WIN_SEC * fs);
            const locH = argmaxRange(self.bufC, candM.idx - half, candM.idx + half + 1);
            const okM = candM.val >= GAPFILL_LOW_SCALE * THR_SIG;
            const okH = locH.val >= GAPFILL_LOW_SCALE * THR_SIG1;

            const seg = R(0.07 * fs);
            const s1 = Math.max(0, candM.idx - seg),
              e1 = candM.idx;
            const s2 = Math.max(0, prev - seg - 1),
              e2 = prev;
            const slope1 = avgDiffSegment(self.bufB, s1, e1);
            const slope2 = avgDiffSegment(self.bufB, s2, e2);
            const vetoTW = Math.abs(slope1) <= Math.abs(T_WAVE_SLOPE_RATIO * slope2);

            if (
              !vetoTW &&
              okM &&
              okH &&
              locH.val > 0 &&
              isUphillPeak(self.bufC, locH.idx, 4, 4) &&
              postPeakDropOK(self.bufC, locH.idx, fs)
            ) {
              added.push(locH.idx);
            }

            if (
              okM &&
              okH &&
              locH.val > 0 &&
              isUphillPeak(self.bufC, locH.idx, 4, 4) &&
              postPeakDropOK(self.bufC, locH.idx, fs)
            ) {
              added.push(locH.idx);
            }
          }
        }
      }
      if (added.length) {
        peaks = peaks.concat(added).sort((a, b) => a - b);
        peaks = dedupPeaks(peaks, R(DEDUP_SEC * fs));
      }
      qrs_i_raw = peaks;
    })(this);

    const out = new Array(qrs_i_raw.length);
    for (let i = 0; i < qrs_i_raw.length; i++) {
      const v = R(qrs_i_raw[i]);
      out[i] = Math.max(0, Math.min(n - 1, v));
    }
    return out;
  }

  rpeak_detection(ecg400Input) {
    const ecg400 =
      ecg400Input instanceof Float32Array ? ecg400Input : new Float32Array(ecg400Input);
    const ecg200 = this.downsample400to200(ecg400);
    const r200 = this.rpeak_detection_200_pos(ecg200, 200);
    const refined = r200
      .map((i200) => {
        const guess = i200 * 2;
        const s = Math.max(0, guess - 2);
        const e = Math.min(ecg400.length, guess + 3);
        const loc = argmaxRange(ecg400, s, e);
        return loc.idx >= 0 && loc.val > 0 && isUphillPeak(ecg400, loc.idx, 4, 4) ? loc.idx : guess;
      })
      .sort((a, b) => a - b);

    const out = dedupPeaks(refined, R(DEDUP_SEC * 400));
    if (out.length >= 2) {
      let minSamp = Infinity;
      for (let i = 1; i < out.length; i++) {
        const d = out[i] - out[i - 1];
        if (d < minSamp) minSamp = d;
      }
      this.minRRsamples = minSamp;
      this.minRRsec = minSamp / 400;
      this.minRRms = this.minRRsec * 1000;
    } else {
      this.minRRsamples = this.minRRsec = this.minRRms = null;
    }

    return out;
  }
}

export { Pan_Tompkins_Plus_Plus_Plus };
